import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent {

	private cards = {
		copper:		new Card("Copper", cardType.treasure, 10,
			(player: Player, count: number) => player.money += count),
		silver:		new Card("Silver", cardType.treasure, 30, 
			(player: Player, count: number) => player.money += 2 * count),
		gold:		new Card("Gold", cardType.treasure, 50, 
			(player: Player, count: number) => player.money += 3 * count),
		estate:		new Card("Estate", cardType.victory, 20, 
			(player: Player, count: number) => player.victory += 1 * count),
		duchy:		new Card("Duchy", cardType.victory, 50, 
			(player: Player, count: number) => player.victory += 3 * count),
		province:	new Card("Province", cardType.victory, 80, 
			(player: Player, count: number) => player.victory += 6 * count),
		village:	new Card("Village", cardType.action, 30, 
			(player: Player, count: number) => {
				player.actionsLeft += 2 * count;
				player.drawCards(count);
			}),
		laboratory:	new Card("Laboratory", cardType.action, 50, 
			(player: Player, count: number) => {
				player.actionsLeft += count;
				player.drawCards(2 * count);
			}),
		smithy:		new Card("Smithy", cardType.action, 40, 
			(player: Player, count: number) => {
				player.drawCards(3 * count);
			})
	};
	private defaultDeck = [
		this.cards.copper, 
		this.cards.copper,
		this.cards.copper,
		this.cards.copper,
		this.cards.copper,
		this.cards.copper,
		this.cards.copper,
		this.cards.estate,
		this.cards.estate,
		this.cards.estate
	];
	private turnActions = [];
	private speed = 3000;
	private press = false;
	private buyList = Object.values(this.cards);

	private victoryGoal = 2000;

	private player = new Player();

  	private ngOnInit() {
  		this.player.deck = this.defaultDeck.slice();
  		setTimeout(() => this.tick(), 0);
  	}

  	private tick() {
  		this.turnActions = [];
  		this.player.newTurn();

  		let treasures = 0;
  		for(let i = 0; i < this.player.hand.length; i++) {
  			const next = this.player.hand[i];
  			if(next.type !== cardType.action || this.player.actionsLeft > 0) {
  				if(next.type) this.player.actionsLeft--;
  				this.player.hand[i].effect(this.player, 1);
  				this.turnActions.push("Played " + next.name);  			
  			}
  		}
  		if(this.player.victory > this.victoryGoal) {
  			this.press = true;
  		}
  		setTimeout(() => this.tick(), this.speed);
  	}

  	private prestiege() {
		this.speed /= 2;
		this.press = false;
		this.player.prestiege();
		this.player.deck = this.defaultDeck.slice();
  	}


  	public getStyle(card: Card) {
  		switch(card.type){
  			case cardType.treasure:
  				return "#ff3";
  			case cardType.victory:
  				return "#3f3";
  			case cardType.action:
  				return "#77f";
  		}
  	}

}

class Player {
	public deck: Card[] = [];
	public hand: Card[] = [];
	public handSize = 5;
	public actions = 1;
	public actionsLeft;
	public turnCount = 0;
	public money = 0;
	public victory = 0;
	public prestiegeCount = 0;
	public cardsInHand = 0;

	public reset() {
		this.deck = [];
		this.hand = [];
		this.turnCount = 0;
		this.money = 0;
		this.victory = 0;
	}

	public prestiege() {
		this.reset();
		this.actions++;
		this.handSize++;
		this.prestiegeCount++;
	}

	public addToDeck(card: Card) {
		this.deck.push(card);
	}

	public buy(card) {
  		if(this.money >= card.cost){
	  		this.money -= card.cost;
	  		this.deck.push(card);	
  		}
  	}

	private shuffleArray(array: any[]) {
		for (let i = array.length - 1; i > 0; i--) {
	        const j = Math.floor(Math.random() * (i + 1));
	        [array[i], array[j]] = [array[j], array[i]];
  		}
  	}

  	public drawCards(cards: number) {
  		this.cardsInHand += cards;
  		this.hand = this.deck.slice(0, this.cardsInHand);
  	}

  	public newTurn() {
  		this.shuffleArray(this.deck);
  		this.cardsInHand = 0;
  		this.drawCards(this.handSize);
  		this.turnCount++;
  		this.actionsLeft = this.actions;
  	}

}

class Card {

	public name: string;
	public type: cardType;
	public effect: any;
	public cost: number;

	constructor(name: string, type: cardType, cost: number, effect: any) {
		this.name = name;
		this.type = type;
		this.cost = cost;
		this.effect = effect;
	}

	public getCardType(array: Card[], type: cardType): Card[] {
  		return array.filter((card: Card) => card.type === type);
  	}
}

enum cardType {
	treasure,
	action,
	victory
}